const init = ({ canvas }) => {
  const backgroundColorTool = document.querySelector("#background-color-tool");
  const backgroundColorOption = document.querySelector(
    "#background-color-option"
  );
  const backgroundColorInput = document.querySelector(
    "#background-color-input"
  );

  backgroundColorInput.addEventListener("input", function (e) {
    canvas.setDimensions({ backgroundColor: e.target.value });
    canvas.renderAl();
  });

  return {
    backgroundColorTool,
    backgroundColorOption,
    backgroundColorInput,
  };
};

export { init };
