const init = ({
  textTool,
  addText,
  canvas,
  imageTool,
  downloadTool,
  backgroundColorTool,
  backgroundColorOption,
  backgroundColorInput,
  canvasSizeButton,
  canvasSizeOption,
}) => {
  const tools = document.getElementsByClassName("tool");
  Array.from(tools).map((tool) => {
    tool.addEventListener("click", function () {
      switch (this.id) {
        case textTool.id:
          return addText();
        case imageTool.id:
          return imageTool.children[2].classList.add("show");
        case downloadTool.id:
          return downloadTool.children[2].classList.add("show");
        case backgroundColorTool.id:
          return backgroundColorOption.classList.add("show");
        case canvasSizeButton.id:
          return canvasSizeOption.classList.add("show");
        case "clear-tool":
          canvas.clear();
          backgroundColorInput.value = "#ffffff";
          break;
        default:
          return;
      }
    });
  });
  return { tools };
};

export { init };
