import getMarkup from "./markup";
import { init as initTools } from "./tools";
import { init as initCanvas } from "./canvas";
import { init as initToolOptions } from "./tool-options";
import { init as initZoom } from "./zoom";
import { init as initText } from "./text";
import { init as initImage } from "./image";
import { init as initBackground } from "./background";
import { init as initDownload } from "./download";
import { init as initCanvasSize } from "./canvas-size";

export default class App {
  constructor({ root }) {
    root.innerHTML = getMarkup();
    const {
      canvas,
      canvasContainer,
      canvasHeightInformation,
      canvasWidthInformation,
    } = initCanvas();
    const {
      canvasSizeButton,
      canvasSizeOption,
      canvasSizeWidthInput,
      canvasSizeHeightInput,
    } = initCanvasSize({ canvas });
    const {
      backgroundColorOption,
      backgroundColorTool,
      backgroundColorInput,
    } = initBackground({
      canvas,
    });
    const { downloadTool } = initDownload({ canvas });
    const { imageTool } = initImage({ canvas });
    const {
      textFontOption,
      textColorOption,
      textLetterSpacingOption,
      textSkewOption,
    } = initToolOptions({
      imageTool,
      backgroundColorOption,
      backgroundColorTool,
      downloadTool,
    });
    const { textTool, addText } = initText({
      canvas,
      textFontOption,
      textColorOption,
      textLetterSpacingOption,
      textSkewOption,
    });
    initTools({
      canvas,
      textTool,
      addText,
      imageTool,
      downloadTool,
      backgroundColorOption,
      backgroundColorTool,
      canvasSizeButton,
      canvasSizeOption,
      backgroundColorInput,
    });

    initZoom({
      canvas,
      canvasContainer,
      canvasHeightInformation,
      canvasWidthInformation,
    });
  }
}

window.CanvasEditor = App;
