const init = ({
  canvas,
  canvasContainer,
  canvasHeightInformation,
  canvasWidthInformation,
}) => {
  const zoomIn = document.querySelector("#zoom-in");
  const zoomOut = document.querySelector("#zoom-out");
  const zoomReset = document.querySelector("#zoom-reset");

  zoomIn.addEventListener("click", function () {
    const currentZoom = canvas.getZoom();
    canvas.setZoom(currentZoom + 0.1);
    canvas.setWidth(canvas.width * canvas.viewportTransform[0]);
    canvas.setHeight(canvas.height * canvas.viewportTransform[3]);
    canvasWidthInformation.innerHTML = parseInt(canvas.width);
    canvasHeightInformation.innerHTML = parseInt(canvas.height);
    canvas.renderAll();
  });

  zoomOut.addEventListener("click", function () {
    const currentZoom = canvas.getZoom();
    canvas.setWidth(canvas.width / canvas.viewportTransform[0]);
    canvas.setHeight(canvas.height / canvas.viewportTransform[3]);
    canvas.setZoom(currentZoom - 0.1);
    canvasWidthInformation.innerHTML = parseInt(canvas.width);
    canvasHeightInformation.innerHTML = parseInt(canvas.height);
    canvas.renderAll();
  });

  zoomReset.addEventListener("click", function () {
    canvas.setZoom(1);
    canvas.setWidth(canvasContainer.offsetWidth);
    canvas.setHeight(canvasContainer.offsetHeight);
    canvasWidthInformation.innerHTML = parseInt(canvas.width);
    canvasHeightInformation.innerHTML = parseInt(canvas.height);
    canvas.renderAll();
  });
};
export { init };
