import { fabric } from "fabric";
import { init as initFontPicker } from "./font-picker";

const init = ({
  canvas,
  textFontOption,
  textColorOption,
  textLetterSpacingOption,
  textSkewOption,
}) => {
  const fontPicker = initFontPicker();
  const textTool = document.querySelector("#text-tool");
  const textSettings = document.querySelector("#text-setting");
  const textColorInput = document.querySelector("#text-color-input");
  const textLetterSpacingInput = document.querySelector(
    "#letter-spacing-input"
  );
  const textSkewHorizontalInput = document.querySelector(
    "#skew-horizontal-input"
  );
  const textSkewVerticalInput = document.querySelector("#skew-vertical-input");

  const waitForFontLoaded = (fontName) => {
    return new Promise((resolve, reject) => {
      const timerId = setInterval(() => {
        if (document.fonts.check(fontName)) {
          clearInterval(timerId);
          resolve();
        }
      }, 200);
    });
  };

  const addText = () => {
    const text = new fabric.IText("こんにちは", {
      fontSize: 20,
      fontFamily: "Open Sans",
      // backgroundColor: '#ffffff',
      fill: "#000000",
    });

    // text.toSVG = function() {
    //   console.log(this);
    //   return `<text fill='${this.fill}'>${this.text}</text>`;
    // };

    canvas.add(text);
    canvas.setActiveObject(text);
    canvas.getActiveObject().center();
    canvas.getActiveObject().setCoords();
    canvas.renderAll();
  };

  Array.from(textSettings.children).map((button) => {
    button.addEventListener("click", function (e) {
      const { id } = e.currentTarget;

      if (id === "text-font") {
        const activeObject = canvas.getActiveObject();
        textFontOption.classList.add("show");
        fontPicker.setActiveFont(activeObject.fontFamily || "Open Sans");
      }

      if (id === "text-color") {
        const activeObject = canvas.getActiveObject();
        textColorOption.classList.add("show");
        textColorInput.value = activeObject.fill;
      }

      if (id === "text-letter-spacing") {
        const activeObject = canvas.getActiveObject();
        textLetterSpacingOption.classList.add("show");
        textLetterSpacingInput.value = activeObject.charSpacing;
      }

      if (id === "text-skew") {
        const activeObject = canvas.getActiveObject();
        textSkewOption.classList.add("show");
        textSkewHorizontalInput.value = activeObject.skewX;
        textSkewVerticalInput.value = activeObject.skewY;
      }
    });
  });

  textLetterSpacingInput.addEventListener("input", function (e) {
    const activeObject = canvas.getActiveObject();
    activeObject.set("charSpacing", e.target.value);
    canvas.renderAll();
  });

  textSkewHorizontalInput.addEventListener("input", function (e) {
    const activeObject = canvas.getActiveObject();
    activeObject.set("skewX", e.target.value);
    canvas.renderAll();
  });

  textSkewVerticalInput.addEventListener("input", function (e) {
    const activeObject = canvas.getActiveObject();
    activeObject.set("skewY", e.target.value);
    canvas.renderAll();
  });

  textColorInput.addEventListener("input", function (e) {
    const activeObject = canvas.getActiveObject();
    activeObject.set("fill", e.target.value);
    canvas.renderAll();
  });

  fontPicker.fontManager.onChange = function (e) {
    const fontFamily = e.family;

    waitForFontLoaded(`16px ${fontFamily}`).then(function () {
      canvas.getActiveObject().set("fontFamily", fontFamily);
      canvas.renderAll();
    });
  };
  return { textTool, textSettings, addText };
};

export { init };
