const init = ({
  imageTool,
  backgroundColorOption,
  backgroundColorTool,
  downloadTool,
}) => {
  const textSettings = document.querySelector("#text-setting");
  const closeOptionButtons = document.getElementsByClassName("close-option");
  const options = document.querySelectorAll(".option");
  const textFontOption = document.querySelector("#text-font-option");
  const textColorOption = document.querySelector("#text-color-option");
  const textLetterSpacingOption = document.querySelector(
    "#text-letter-spacing-option"
  );
  const textSkewOption = document.querySelector("#text-skew-option");
  const importImageFromLocalOption = document.querySelector(
    "#import-image-from-local-option"
  );
  const importImageFromUrlOption = document.querySelector(
    "#import-image-from-url-option"
  );

  Array.from(closeOptionButtons).map((button) => {
    button.addEventListener("click", function (e) {
      const toolOption = this.parentElement.parentElement;
      toolOption.classList.remove("show");
    });
  });

  document.addEventListener("click", function (e) {
    Array.from(options).map(() => {
      if (
        !textFontOption.contains(e.target) &&
        !textSettings.children[0].contains(e.target)
      ) {
        textFontOption.classList.remove("show");
      }

      if (
        !textColorOption.contains(e.target) &&
        !textSettings.children[1].contains(e.target)
      ) {
        textColorOption.classList.remove("show");
      }

      if (
        !textLetterSpacingOption.contains(e.target) &&
        !textSettings.children[2].contains(e.target)
      ) {
        textLetterSpacingOption.classList.remove("show");
      }

      if (
        !textSkewOption.contains(e.target) &&
        !textSettings.children[3].contains(e.target)
      ) {
        textSkewOption.classList.remove("show");
      }

      if (
        !backgroundColorOption.contains(e.target) &&
        !backgroundColorTool.contains(e.target)
      ) {
        backgroundColorOption.classList.remove("show");
      }

      if (
        !importImageFromLocalOption.contains(e.target) &&
        !imageTool.contains(e.target)
      ) {
        importImageFromLocalOption.classList.remove("show");
      }

      if (
        !importImageFromUrlOption.contains(e.target) &&
        !imageTool.contains(e.target)
      ) {
        importImageFromUrlOption.classList.remove("show");
      }
    });

    if (!imageTool.contains(e.target)) {
      imageTool.children[2].classList.remove("show");
    }

    if (!downloadTool.contains(e.target)) {
      downloadTool.children[2].classList.remove("show");
    }
  });

  return {
    closeOptionButtons,
    textFontOption,
    textColorOption,
    textLetterSpacingOption,
    textSkewOption,
    importImageFromLocalOption,
    importImageFromUrlOption,
  };
};

export { init };
