import { fabric } from "fabric";
import {
  rotateIcon,
  moveIcon,
  editIcon,
  duplicateIcon,
  scaleIcon,
  deleteIcon,
} from "./icons";

const init = () => {
  const textSettings = document.querySelector("#text-setting");
  const canvas = new fabric.Canvas("canvas");
  const canvasContainer = document.querySelector(".my-canvas");
  const canvasWidthInformation = document.querySelector("#canvas-width");
  const canvasHeightInformation = document.querySelector("#canvas-height");

  const canvasWidth = canvasContainer.offsetWidth;
  const canvasHeight = canvasContainer.offsetHeight;

  canvas.setWidth(canvasWidth);
  canvas.setHeight(canvasHeight);
  canvas.backgroundColor = "#ffffff";
  canvasWidthInformation.innerHTML = canvasWidth;
  canvasHeightInformation.innerHTML = canvasHeight;

  canvas.on("text:editing:entered", function (e) {
    textSettings.classList.add("show");
  });

  canvas.on("text:editing:exited", function () {
    textSettings.classList.remove("show");
  });

  function renderIcon(icon) {
    return function renderIcon(ctx, left, top, styleOverride, fabricObject) {
      ctx.save();
      ctx.translate(left, top);
      ctx.shadowColor = "rgba(0,0,0,.15)";
      ctx.shadowBlur = 4;
      ctx.shadowOffsetX = 0;
      ctx.shadowOffsetY = 0;
      ctx.drawImage(icon, -15, -15, 30, 30);
      ctx.restore();
    };
  }

  function deleteObject(eventData, target) {
    if (target._objects && target._objects.length) {
      const activeObject = canvas.getActiveObjects();
      canvas.discardActiveObject();
      canvas.remove(...activeObject);
      return;
    }

    canvas.remove(target);
    canvas.renderAll();
  }

  function cloneObject() {
    const activeObject = canvas.getActiveObject();
    let clipboard = null;

    if (activeObject.text) {
      activeObject.clone(function (cloned) {
        clipboard = cloned;
      });
    } else {
      clipboard = activeObject;
    }

    activeObject.clone(function (cloned) {
      clipboard = cloned;
    });

    clipboard.clone(function (clonedObj) {
      canvas.discardActiveObject();

      clonedObj.set({
        evented: true,
      });

      clonedObj.animate("top", clonedObj.top + 30, {
        onChange: canvas.renderAll.bind(canvas),
        duration: 500,
        easing: fabric.util.ease.easeOutBounce,
      });

      clonedObj.animate("left", clonedObj.left + 30, {
        onChange: canvas.renderAll.bind(canvas),
        duration: 500,
        easing: fabric.util.ease.easeOutBounce,
      });

      if (clonedObj.type === "activeSelection") {
        // active selection needs a reference to the canvas.
        clonedObj.canvas = canvas;
        clonedObj.forEachObject(function (obj) {
          canvas.add(obj);
        });
        // this should solve the unselectability
        clonedObj.setCoords();
      } else {
        canvas.add(clonedObj);
      }

      canvas.setActiveObject(clonedObj);
      canvas.requestRenderAll();
    });
  }

  fabric.Object.prototype.transparentCorners = true;
  fabric.Object.prototype.cornerSize = 30;
  fabric.Object.prototype.setControlsVisibility({
    mtr: false,
    mt: false,
    mb: false,
    ml: false,
    mr: false,
    bl: false,
    br: false,
    tl: false,
    tr: false,
  });
  fabric.Object.prototype.borderColor = "#666b6b";
  fabric.Object.prototype.borderDashArray = [2, 5];
  fabric.Object.prototype.borderScaleFactor = 2;
  fabric.Object.prototype.padding = 10;

  fabric.Object.prototype.controls.rotateControl = new fabric.Control({
    x: 0.5,
    y: -0.5,
    cursorStyle: "pointer",
    actionHandler: fabric.controlsUtils.rotationWithSnapping,
    render: renderIcon(rotateIcon),
  });

  fabric.Object.prototype.controls.moveControl = new fabric.Control({
    x: -0.5,
    y: -0.5,
    cursorStyle: "pointer",
    actionHandler: fabric.controlsUtils.dragHandler,
    render: renderIcon(moveIcon),
  });

  fabric.Object.prototype.controls.editControl = new fabric.Control({
    x: 0,
    y: -0.5,
    cursorStyle: "pointer",
    mouseUpHandler: function (e, object) {
      object.enterEditing();
    },
    render: renderIcon(editIcon),
  });

  fabric.Object.prototype.controls.scaleControl = new fabric.Control({
    x: 0.5,
    y: 0.5,
    cursorStyle: "pointer",
    actionHandler: fabric.controlsUtils.scalingEqually,

    // mouseUpHandler: deleteObject,
    render: renderIcon(scaleIcon),
  });

  fabric.Object.prototype.controls.duplicateIcon = new fabric.Control({
    x: 0,
    y: 0.5,
    cursorStyle: "pointer",
    mouseUpHandler: cloneObject,
    render: renderIcon(duplicateIcon),
  });

  fabric.Object.prototype.controls.deleteControl = new fabric.Control({
    x: -0.5,
    y: 0.5,
    cursorStyle: "pointer",
    mouseUpHandler: deleteObject,
    render: renderIcon(deleteIcon),
    cornerSize: 28,
  });
  return {
    canvas,
    canvasContainer,
    canvasWidthInformation,
    canvasHeightInformation,
  };
};

export { init };
