const init = ({ canvas }) => {
  const importImageFromLocalOption = document.querySelector(
    "#import-image-from-local-option"
  );
  const importImageFromUrlOption = document.querySelector(
    "#import-image-from-url-option"
  );
  const imageTool = document.querySelector("#image-tool");

  const importImageFromLocalTool = document.querySelector(
    "#import-image-from-local-tool"
  );
  const importImageFromUrlTool = document.querySelector(
    "#import-image-from-url-tool"
  );
  const imageSetting = document.querySelector("#image-setting");
  const imageImportFromLocalInput = document.querySelector(
    "#import-image-from-local-input"
  );
  const imageImportFromUrlInput = document.querySelector(
    "#import-image-from-url-input"
  );
  const imageImportFromUrlButton = document.querySelector(
    "#import-image-from-url-button"
  );

  function addImage(url) {
    fabric.Image.fromURL(url, function (oImg) {
      oImg.scaleToWidth(canvas.width / 2);
      oImg.scaleToHeight(canvas.height / 2);
      canvas.add(oImg);
    });
  }

  imageImportFromLocalInput.addEventListener("change", function (e) {
    const reader = new FileReader();
    reader.readAsDataURL(e.target.files[0]);
    reader.onload = function () {
      const imageUrl = reader.result;
      addImage(imageUrl);
      importImageFromLocalOption.classList.remove("show");
      imageImportFromLocalInput.value = "";
    };
    reader.onerror = function (error) {
      console.log("Error: ", error);
    };
  });

  imageImportFromUrlButton.addEventListener("click", function () {
    const url = imageImportFromUrlInput.value;

    const xhr = new XMLHttpRequest();
    xhr.onload = function () {
      const reader = new FileReader();
      reader.onloadend = function () {
        addImage(reader.result);
        importImageFromUrlOption.classList.remove("show");
        imageImportFromUrlInput.value = "";
      };
      reader.readAsDataURL(xhr.response);
    };
    const proxyUrl = "https://cors-anywhere.herokuapp.com/";
    xhr.open("GET", proxyUrl + url);
    xhr.responseType = "blob";
    xhr.send();
  });

  importImageFromLocalTool.addEventListener("click", function () {
    importImageFromLocalOption.classList.add("show");
  });

  importImageFromUrlTool.addEventListener("click", function () {
    importImageFromUrlOption.classList.add("show");
  });

  return {
    imageTool,
    imageSetting,
  };
};

export { init };
