const init = ({ canvas }) => {
  const downloadTool = document.querySelector("#download-tool");
  const downloadToJPEGButton = document.querySelector("#download-to-jpeg-tool");
  const downloadToSVGButton = document.querySelector("#download-to-svg-tool");

  function downloadToSVG() {
    const svg = canvas.toSVG();

    const a = document.createElement("a");
    a.href = "data:image/svg+xml;utf-8," + encodeURIComponent(svg);
    a.download = "my-image.svg";
    a.target = "_blank";

    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
  }

  function downloadToJPEG() {
    const jpeg = canvas.toDataURL({
      format: "jpeg",
    });

    const a = document.createElement("a");
    a.href = jpeg;
    a.download = "my-image.jpeg";
    a.target = "_blank";

    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
  }

  downloadToJPEGButton.addEventListener("click", function () {
    downloadToJPEG();
  });

  downloadToSVGButton.addEventListener("click", function () {
    downloadToSVG();
  });

  return { downloadTool };
};

export { init };
